<template>

	<view class="content pt10">
		<view class="pl15 pr15 bgcf">
			<view class="flex aic  b-b-1 pb10 pt10">
				<view class="flex aic pr35 fwb">
					<text style="color: #F43B31;">*</text>
					<text class="fs15 pl5">联系人</text>
				</view>
				<view class="pl30">
					<input type="text" placeholder="请输入联系人" v-model="ming" placeholder-class="fs14 cb" />
				</view>
			</view>
			<view class="flex aic pb10 b-b-1 pt10">
				<view class="flex aic pr35 fwb">
					<text style="color: #F43B31;">*</text>
					<text class="fs15 pl5">性别</text>
				</view>
				<view class="pl35 ml10">
					<radio-group @change="radioChange" class="flex">
						<label v-for="(item, index) in items" :key="item.value">
							<radio :value="item.value" :checked="index === current" />
							<text class="fs13 pr20">{{item.name}}</text>
						</label>
					</radio-group>
				</view>
			</view>
			<view class="flex aic   pb10 pt10">
				<view class="flex aic pr30 fwb">
					<text style="color: #F43B31;">*</text>
					<text class="fs15 pl5">手机号码</text>
				</view>
				<view class="pl20 ">
					<input type="text" placeholder="请输入手机号码" v-model="daib" placeholder-class="fs14 cb" />
				</view>
			</view>
		</view>

		<view class="bgcf pl15 pr15 mt10">
			<wangding-pickerAddress class="  " @change="change">
				<view class="flex aic ju_b b-b-1  pb10 pt10">
					<view class="flex aic fwb">
						<text style="color: #F43B31;">*</text>
						<text class="fs15 pl5">所在地区</text>
					</view>
					<view class=" ">
						<text class="fs14  cb pr35 mr20" style="margin-left: 8upx;">{{city}}</text>

					</view>
					<text class=" icon-arrow iconfont fs11 cb pl5"></text>
				</view>
			</wangding-pickerAddress>
			<view class="flex aic ju_b pb12 pt12 b-b-1">
				<view class="dis_flex">
					<view class="flex aic fwb">
						<text style="color: #F43B31;">*</text>
						<text class="fs15 pl5">详细地址</text>
					</view>
					<view class="pr5" style="margin-left: 102upx;">
						<input type="text" placeholder="请选择详细街道、楼牌号等" v-model="daib" placeholder-class="fs14 cb" />
					</view>
				</view>
				<view class=" icon-arrow iconfont fs11 cb prl5 "></view>
			</view>
<view class="flex aic ju_b  pb12 pt12 b-b-1">
				<view class="flex aic">
					<text class="fs15 pl5 fwb">职位</text>
				</view>
				<view class="" style="width: 235rpx;">
					<picker @change="bindPickerChange" :range="array">

						<label class="fs14 ">{{array[index]}}</label>

					</picker>
				</view>
				<view class=" icon-arrow iconfont fs11 cb pl5 "></view>
			</view>
			<view class="flex ju_b  pb12 pt12 aic">
				<text class="fs15 fwb">设为默认地址</text>
				
			<switch class="section-right" :checked="treatmentStatu.checked" @change="switchChange" color="#F43B31" style="transform: scale(.98,.82);" />
			
			</view>
		</view>

		<!-- <view class="bgcf pl15 pr15 mt10">
			<view class="fs15 pt15">
				个人简介
			</view>
			<textarea value="" placeholder="请输入您的个人简介…"  class="fs14 pt10"/>
		</view> -->
		<view class="flex  pt25 absolute bottom15" v-if="tile=='新增联系人信息'">
			
			<view class="vaoc flex aic ju_c cf ml15 fs15" @click="bbaoc">
				保存
			</view>
		</view>
		<view class=""v-else>
			
		</view>
	</view>
</template>

<script>
	import api from '../../components/service.js';
	import yudatetimepicker from '../../components/yu-datetime-picker/yu-datetime-picker.vue'
	var that
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		components: {
			yudatetimepicker
		},
		data() {
			const currentDate = this.getDate({
				format: true
			})

			return {
				treatmentStatu: {
					checked: false
				},
				current: 0,
				list: ['a', 'b', 'c', 'd'],
				changeid: 1,
				money: 0,
				showxq2: false,			
				items: [{
						// value: 'aa',
						name: '男'
					},
					{
						// value: 'bb',
						name: '女',
						checked: 'true'
					},
				],
				shenf: '',
				daib: '',
				daim: '',
				ming: '',
				jgg: '',
				indexg: 0,
				jg: '',
				zijin: '',
				array: ['企业法人', '企业', '自媒体'],
				index: 0,
				city: '请选择省/市/区县',
				date: '请选择省/市/区县',
				tile:'',
			}
		},

		onHide() {

		},
		onLoad(option) {
			that = this
			console.log(option)
			if(option.id==1) {
				that.tile='联系人信息'
			}else {
				that.tile='新增联系人信息'
			}
			uni.setNavigationBarTitle({
			　　title:that.tile
			})
		},
		onShow() {

		},

		computed: {
			startDate() {
				return this.getDate('start');
			},
			endDate() {
				return this.getDate('end');
			},
			...mapState(['hasLogin', 'forcedLogin', 'userName', 'userinfo'])
		},
		methods: {
			...mapMutations(['logout', 'login']),
			getimg(img) {
				return api.getimg(img)
			},
			jump(e) {
				return api.jump(e)
			},
			bindDateChange: function(e) {
				this.date = e.target.value
			},
			bindPickerChange: function(e) {
				this.index = e.target.value
				that.jg = this.array[this.index]
				console.log(that.jg)
			},
			
			change(data) {

				this.city = data.data.join('')
				console.log(data.data.join(''))
				console.log(this.city);
			},
			bindDateChange: function(e) {
				this.date = e.target.value
			},
			switchChange() {
				uni.showModal({
					title: '提示',
					content: '确定设置为默认地址吗',
					success: function(res) {
						if (res.confirm) {
							console.log('用户点击确定');
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					},
				})
			},
			
			getDate(type) {
				const date = new Date();
				let year = date.getFullYear();
				let month = date.getMonth() + 1;
				let day = date.getDate();

				if (type === 'start') {
					year = year - 60;
				} else if (type === 'end') {
					year = year + 2;
				}
				month = month > 9 ? month : '0' + month;;
				day = day > 9 ? day : '0' + day;
				return `${year}-${month}-${day}`;
			},
			bbaoc() {
				uni.showToast({
					title: '保存成功',
					icon: 'none',
				});
				setTimeout(function() {
					wx.navigateBack({
						delta: 1
					})
				}, 1000)
			},
			radioChange: function(evt) {
				for (let i = 0; i < this.items.length; i++) {
					if (this.items[i].value === evt.target.value) {
						this.current = i;
						break;
					}
				}
			},
			changeys(index) {
				this.changeid = index + 1;
			},
			xq2() {
				if (this.showxq2 == false) {
					this.showxq2 = true;
				} else {
					this.showxq2 = false;
				}
			}
		}
	}
</script>

