	}

	.inpurd {
		width: 400rpx;
		color: #DDDDDD !important;
	}

	.time {
		padding-left: 30rpx;
	}

	.time:before {
		content: '';
		position: absolute;
		width: 5rpx;
		height: 25rpx;
		left: 39rpx;
